@use 'variables' as v;

html{
    font-size: 62.5%;
    box-sizing: border-box;

    scroll-behavior: smooth;

}
*, *:before, *:after{
    box-sizing: inherit;
}
body{
        font-family: Arial, sans-serif;
        margin: 0;
        padding: 0;
        background-color: v.$light-color;

    

    font-family: v.$fuente_principal;
    font-size: 1.6rem;

    &.body-scroll{
        padding-top: 8.6rem;
        
    }

    &.fijar-body{
        overflow: hidden;
        
    }
}

p {
    font-size: 2rem;
    color: v.$negro;
    margin: 2rem;
    line-height: 1.5;
}
.contenedor{
    max-width: 120rem;
    width: 95%;
    margin: 0 auto;
}

a{
    text-decoration: none;

}

h1,h2,h3{
    margin: 0 0 5rem 0;
}
h1{
    font-size: 4rem;
}
h2{
    font-size: 3.6rem;
}
h3{
    font-size: 3rem;
}

